<template>
  <div class="main">
    <header>
      <!-- <h4> -  - </h4> -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path:'/manage/projectlist'}"></el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path:'/manage/projectlist'}"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>      
    </header>
    <nav>
      <span></span>
    </nav>
    <!-- && -->
    <el-tabs type="border-card">
      <el-tab-pane label="">
        <!--  -->
        <section>
          <el-table :data="tableData" style="width: 100%;padding-left: 100px">
            <el-table-column prop="name" label="" width="180">
            </el-table-column>        
            <el-table-column prop="phone" label="" width="180">
            </el-table-column>
            <el-table-column prop="equity" label="" width="180">
            </el-table-column>
          </el-table>
        </section>
        <footer>
          <el-upload class="upload-demo" action="https://jsonplaceholder.typicode.com/posts/" :on-preview="handlePreview" :on-remove="handleRemove" :before-remove="beforeRemove" multiple :file-list="fileList" accept=".xlsx" :limit="1" :on-exceed="handleExceed">
            <el-button plain icon="el-icon-upload2" size="small" type="primary"></el-button>
            <div slot="tip" class="el-upload__tip">.xlsx</div>
            <div slot="tip" class="el-upload__tip"></div>
          </el-upload>
        </footer>        
      </el-tab-pane>
      <el-tab-pane label="">
        <!--  -->
        <section>
          <el-table :data="tableData2" style="width: 100%;padding-left: 100px">
            <el-table-column prop="transferor" label="" width="180">
            </el-table-column>        
            <el-table-column prop="assignee" label="" width="180">
            </el-table-column>
            <el-table-column prop="time" label="" width="180">
            </el-table-column>
          </el-table>
        </section>
        <footer class="myForm">
          <el-form ref="form" :model="form" label-width="80px">
            <el-form-item>
              <el-input v-model="form.transferor" placeholder=""></el-input>
            </el-form-item>
            <el-form-item>
              <el-input v-model="form.assignee" placeholder=""></el-input>
            </el-form-item> 
            <el-form-item>
              <el-date-picker type="date" placeholder="" v-model="form.time"></el-date-picker>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="onSubmit"></el-button>
            </el-form-item>
          </el-form>
        </footer>     
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        tableData: [{
          name: '',
          phone: '13632756287',
          equity: '11%'
        }, {
          name: '',
          phone: '13632756287',
          equity: '12%'
        }, {
          name: '',
          phone: '13632756287',
          equity: '9%'
        }, {
          name: '',
          phone: '13632756287',
          equity: '10%'
        }],
        tableData2: [{
          transferor: '',
          assignee: '',
          time: '2019-09-17'
        }, {
          transferor: '',
          assignee: '',
          time: '2019-09-17'
        }, {
          transferor: '',
          assignee: '',
          time: '2019-09-17'
        }, {
          transferor: '',
          assignee: '',
          time: '2019-09-17'
        }],        
        fileList: [],
        form: {
          transferor: '',
          assignee: '',
          time: ''
        }
      }
    },
    methods:{
      handleRemove(file, fileList) {
        console.log(file, fileList);
      },
      handlePreview(file) {
        console.log(file);
      },
      handleExceed(files, fileList) {
        this.$message.warning(` 1  ${files.length}  ${files.length + fileList.length} `);
      },
      beforeRemove(file, fileList) {
        return this.$confirm(` ${ file.name }`);
      },
      onSubmit() {
        console.log('submit!');
        // <!--  -->
        if(true){
          this.$message({
            message: '',
            type: 'success'
          });
          // 
          var date=new Date(this.form.time);
          var y=date.getFullYear();
          var m=date.getMonth()+1;
          m<10&&(m="0"+m);
          var d=date.getDate();
          d<10&&(d="0"+d);
          this.form.time = `${y}-${m}-${d}`;
          // tableData2ajax
          this.tableData2.push({transferor:this.form.transferor,assignee:this.form.assignee,time:this.form.time});
        }else{
          this.$message.error('');
        }
      }
    }
  }
</script>

<style scoped>
/*  */
.main,header{
  width:100%;
  margin-top:-2px;
}

/*  */
.main>nav:nth-child(2){
  font-weight: bold;
  padding:10px 20px 20px 20px;
  background-color:#fff;
}

.main>nav:nth-child(2)>span{
  color:#5167B7;
}

/*  */
.main>footer{
  margin:50px 0 0 20px;
}

/*  */
.myForm>.el-form>.el-form-item{
  display: inline-block;
}

.myForm>.el-form>.el-form-item:nth-child(2)>.el-form-item__content{
  margin-left: 10px!important;
}

</style>



