// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import "@/assets/css/base.css"
import ElementUI from 'element-ui';
//1.1 vueResource(ajax) //vue-resource
import VueResource from 'vue-resource';
//1.2:VueResource  vue  //useVueResource
Vue.use(VueResource);
//1.2.1:()
Vue.http.options.root="http://127.0.0.1:3000/";
//1.2.2:
Vue.filter("datetimeFilter",function(val){
  var date=new Date(val);
  var y=date.getFullYear();
  var m=date.getMonth()+1;
  m<10&&(m="0"+m);
  var d=date.getDate();
  d<10&&(d="0"+d);
  var h=date.getHours();
  h>12&&(h-=12);
  h<10&&(h="0"+h);
  var M=date.getMinutes()
  M<10&&(M="0"+M);
  return `${y}-${m}-${d} ${h}:${M}`;
});

// elementui
import 'element-ui/lib/theme-chalk/index.css';
Vue.use(ElementUI);

Vue.config.productionTip = false

// animate.css  https://www.cnblogs.com/rlann/p/7192535.html
import animate from 'animate.css'
Vue.use(animate);

//2.1:vuex
//vuex
import Vuex from "vuex";
//vuesvue
Vue.use(Vuex);
//vuex[;;]
//new Vuex.Store();
var store=new Vuex.Store({
  state:{//data
    // this.$store.state.***
    //#
    count:1,
    counts:0
  },
  mutations:{
    //#  state mutations
    //mutations 
    //this.$store.commit("increment");
    numUpdate(state){
      state.counts=state.counts+state.count;
      state.count=1
    },
    increment(state){state.count++},
    subtract(state){
      if(state.count>1){
        state.count--
      }
    }//incrementsubtract
  },
  getters:{//getters
    //#getters  
    //{{$store.getters.optCount}}
    optCount:function(state){
      return state.count;
    },
    optCounts:function(state){
      return state.counts
    }
  }
});

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  store,
  template: '<App/>'
})



