<template>
  <div class="main">
    <header>
      <!-- <h4> - </h4> -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path:'/manage/setjurisdiction'}"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>    
      </el-breadcrumb>   
    </header>
    <nav>
      
    </nav>
    <section>
      <el-table :data="tableData" style="width: 100%;">
        <el-table-column prop="people" label="" width="280">
        </el-table-column>        
        <el-table-column prop="list" label="" width="1000">
          <!-- <el-table-column v-for="item in list" :key="item.value" v-text="item"></el-table-column> -->
          <template slot-scope="scope">
            <span size="medium" v-for="s in scope.row.list" :key="s.value" v-text="s" style="padding:10px"></span>
          </template>
        </el-table-column>
        <el-table-column label="" width="120">
            <template slot-scope="scope">
              <el-button @click.native.prevent="deleteRow(scope.$index, tableData)" type="text" style="color:red;" size="small">
                
              </el-button>
              <el-button @click.native.prevent="handleEdit(scope.$index, tableData)" type="text" size="small">
                
              </el-button>
            </template>
          </el-table-column>
      </el-table>
    </section>
    <footer style="padding-top:80px;background:#FFFFFF">
      <el-button plain type="success" style="margin-left:80px" @click.native.prevent="handleJump()">
        
      </el-button>
    </footer>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        tableData: [{
          people: '',
          list: ["","",""]
        }, {
          people: '',
          list: ["","",""]
        }, {
          people: '',
          list: ["","",""]
        }], 
        deleteName: [{
        }]
      }
    },
    methods:{
      handleEdit(index, rows){
        // console.log(rows[index].people);
        this.$router.push("/manage/setamend/"+rows[index].people);
      },
      handleJump(){
        this.$router.push("/manage/setamend");
        console.log(123);
      },
      deleteRow(index, rows) {
        // console.log(rows[index].people);
        // rows.splice(index, 1);
        // this.dialogVisible = true;
        this.deleteName = rows[index].people;
        this.$confirm(` ${this.deleteName} , ?`, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          rows.splice(index, 1);
          this.$message({
            type: 'success',
            message: '!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });          
        });
      }
    }
  }
</script>

<style scoped>
/*  */
.main,header{
  width:100%;
  margin-top:-2px;
}

/*  */
.main>nav:nth-child(2){
  font-weight: bold;
  padding:10px 20px 20px 20px;
  background-color:#fff;
}

.main>nav:nth-child(2)>span{
  color:#5167B7;
}


</style>


