<template>
  <div class="main">
    <header>
      <!-- <h4> -  - </h4> -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path:'/manage/projectlist'}"></el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path:'/manage/projectlist'}"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>        
    </header>
    <section>
      <!--  -->
      <nav>
        <!--  -->
        <!-- <el-dropdown split-button plain @click="handleClick">
          
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item></el-dropdown-item>
            <el-dropdown-item></el-dropdown-item>
            <el-dropdown-item></el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown> -->
        <el-select v-model="value1" placeholder="">
          <el-option v-for="item in options1" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
        <!--  -->
        <!-- <el-input placeholder="" v-model="search" clearable autofocus>
          <i slot="prefix" class="el-input__icon el-icon-search"></i>
        </el-input> -->
        <el-input placeholder="" prefix-icon="el-icon-search" v-model="search" clearable>
        </el-input>
        <!--  -->
        <el-button></el-button>
        <!--  -->
        <!-- <el-select v-model="value2" placeholder="">
          <el-option v-for="item in options2" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select> -->
        <!--  -->
        <el-select v-model="value3" placeholder="" style="width:200px">
          <el-option v-for="item in options3" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>        
      </nav>
      <!--  -->
      <section>
        <!-- <el-table ref="multipleTable" tooltip-effect="dark" @selection-change="handleSelectionChange" :data="tableData.filter(data => !search || data.number.toLowerCase().includes(search.toLowerCase()) || data.name.toLowerCase().includes(search.toLowerCase()))" style="width: 100%"> -->
        <el-table ref="multipleTable" tooltip-effect="dark" @selection-change="handleSelectionChange" :data="tableData" style="width: 100%">
          <el-table-column type="selection" width="30">
          </el-table-column>
          <el-table-column prop="sequence" label="" width="100" sortable>
          </el-table-column>
          <el-table-column label="" width="150" sortable>
            <template slot-scope="scope">
              <el-tag size="medium" style="border:0;background-color:transparent;font-weight:bold;" type="danger" v-show="scope.row.earings<0">{{ scope.row.earings }}</el-tag>
              <el-tag size="medium" style="border:0;background-color:transparent;font-weight:bold;" type="success" v-show="scope.row.earings>=0">{{ scope.row.earings }}</el-tag>
            </template>
          </el-table-column>
          <el-table-column label="" prop="name" width="130" sortable>
          </el-table-column>
          <el-table-column label="" prop="phone" width="120" sortable>
          </el-table-column>
          <el-table-column label="" prop="stock" width="120" sortable>
          </el-table-column>
          <el-table-column label="" prop="time" width="150" sortable>
          </el-table-column>
        </el-table>
      </section>
      <!--  -->
      <footer>
        <div class="left-block">
          <el-button type="danger" icon="el-icon-delete" @click="dialogDelete(tableData)"></el-button>
          <el-button type="success" icon="el-icon-sort" @click="dialogFormVisible = true"></el-button>
        </div>
        <!--  -->
        <el-dialog title="" :visible.sync="dialogVisible" width="30%" :before-close="handleClose" style="text-align:center">
          <span>
            {{" " + deleteName}}
          </span>
          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false"> </el-button>
            <el-button type="primary" @click='deleteSelect(tableData)'> </el-button>
          </span>
        </el-dialog>        
        <div class="right-block">
          <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage4" :page-sizes="[100, 200, 300, 400]" :page-size="100" layout="total, sizes, prev, pager, next, jumper" :total="400">
          </el-pagination>
        </div>
      </footer>
      <!--  -->
      <el-dialog title="" :visible.sync="dialogFormVisible">
        <el-form :model="form">
          <el-form-item label="" :label-width="formLabelWidth">
            <el-select v-model="form.region" placeholder="" style="width:300px">
              <el-option label="" value="shanghai"></el-option>
              <el-option label="" value="beijing"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="form.name" autocomplete="off"></el-input>
          </el-form-item>   
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false"> </el-button>
          <el-button type="primary" @click="dialogFormSubmit()"> </el-button>
        </div>
      </el-dialog>  
    </section>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        search: '',
        tableData: [{
          sequence: '1',
          name: '',
          earings: '+1000',
          phone:13632756286,
          stock:'10%',
          time:'2016-05-02 0900'
        }, {
          sequence: '2',
          name: '',
          earings: '-1000',
          phone:13632756286,
          stock:'10%',
          time:'2016-05-02 0900'
        }, {
          sequence: '3',
          name: '',
          earings: '+1000',
          phone:13632756286,
          stock:'10%',
          time:'2016-05-02 0900'
        }],
        multipleSelection: [],//
        currentPage4: 4,
        UserList: [],
        options1: [{
          value: '1',
          label: ''
        }, {
          value: '2',
          label: ''
        }],
        value1: '',
        // options2: [{
        //   value: '1',
        //   label: ''
        // }, {
        //   value: '2',
        //   label: ''
        // }, {
        //   value: '3',
        //   label: ''
        // }, {
        //   value: '4',
        //   label: ''
        // }],
        // value2: '',
        options3: [{
          value: '1',
          label: ''
        }, {
          value: '2',
          label: ''
        }],
        value3: '',
        dialogFormVisible: false,//
        dialogVisible: false,
        deleteName: [],
        form: {
          name: '',
          address: '',
          person: '',
          phone: '',
          project: '',
          people: ''
        },
        formLabelWidth: '120px'//  
      }
    },
    created(){
      // this.getUserList();
    },
    methods: {
      // ajax
      // getGoodsInfo(){
      //   var id=this.id;
      //   var url="goodslist/find?id="+id;
      //   this.$http.get(url).then(result=>{
      //     if(result.body.code==1){
      //       this.UserList=result.body.list;
      //     }
      //   });
      // },
      // handleClick() {
      //   this.$message.error('');        
      // },
      handleSelectionChange(val) {
        this.multipleSelection = val;
        
      },
      deleteRow(index, rows) {
        // console.log(rows[index]);
        rows.splice(index, 1);
      },
      dialogDelete(tableData){
        this.dialogVisible = true;
        var deleteItem=this.multipleSelection;
        this.deleteName.splice(0);//
        for(var j=0;j<tableData.length;j++){
          for( var i=0;i<deleteItem.length;i++){
            if(tableData[j] == deleteItem[i]){
              this.deleteName.push(deleteItem[i].name);
            }
          }
        }     
      },
      deleteSelect(tableData){
        let deleteItem=this.multipleSelection;
        // console.log(deleteItem);
        for(let j=0;j<tableData.length;j++){
          // console.log(tableData[j].number)
          for( let i=0;i<deleteItem.length;i++){
            if(tableData[j] == deleteItem[i]){
              // console.log(deleteItem[i].num+"+++");
              // console.log(tableData[j].num); 
              // console.log("===========");
              tableData.splice(j, 1);
              // console.log(tableData);
              // console.log(deleteItem[i].name);
              this.$message({
                type: 'success',
                message: ': '
              });
            }
          }
        };
        this.dialogVisible = false;
      },
      handleEdit(index, rows){
        this.$router.push("/manage/details");
      },
      handleSizeChange(val) {
        console.log(` ${val} `);
      },
      handleCurrentChange(val) {
        console.log(`: ${val}`);
      },
      handleClose(done) {//
        this.$confirm('')
          .then(_ => {
            done();
          })
          .catch(_ => {});
      },
      dialogFormSubmit(){
        this.$confirm('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
            message: '!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });   
        }); 
        this.dialogFormVisible =false;       
      }
    }
  }
</script>

<style scoped>
/*  */
.main,header{
  width:100%;
  margin-top:-2px;
}
/*  */
  nav{
    padding:15px;
  }
  /* .el-dropdown {
    vertical-align: top;
  }
  .el-dropdown + .el-dropdown {
    margin-left: 15px;
  } */
  nav div,nav input,nav button{
    margin-left:25px!important;
  }
  .el-icon-arrow-down {
    font-size: 12px;
  }
  .el-input{
    display:inline-block;
    width:300px;
  }
 footer{
    margin:10px;
    padding-right:50px;
    display:flex;   /**/
    justify-content:space-between;
    align-items:center;
 }
  .right-block,.left-block{
    display: inline-block;
  }
  .left-block{
    margin-left:35px;
  }


</style>





