<template>
  <div class="main">
    <header>
      <!-- <h4> -  - {{title}}</h4> -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path:'/manage/setadministrator'}"></el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path:'/manage/setadministrator'}"></el-breadcrumb-item>        
        <el-breadcrumb-item>{{title}}</el-breadcrumb-item>
      </el-breadcrumb>        
    </header>
    <nav>
      {{title}}
    </nav>
    <!--  -->
    <section>
      <el-form label-width="100px" :model="form" :rules="rules" ref="form" status-icon>
        <el-form-item label="" prop="serial">
          <el-input placeholder="" v-model="form.serial"></el-input>
        </el-form-item>
        <el-form-item label="" prop="pass">
          <el-input placeholder="" v-model="form.pass" type="password"></el-input> 
        </el-form-item>
        <el-form-item label="" prop="checkPass">
          <el-input placeholder="" v-model="form.checkPass" type="password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="name">
          <el-input placeholder="" v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="phone">
          <el-input placeholder="" v-model="form.phone"></el-input>
        </el-form-item>
        <el-form-item label="" prop="people">
          <el-select v-model="form.people" placeholder="" style="width:150px;">
            <el-option label="" value="administrator"></el-option>
            <el-option label="" value="market"></el-option>
            <el-option label="" value="operate"></el-option>
            <el-option label="" value="exploit"></el-option>
            <el-option label="" value="operation"></el-option>
          </el-select>
        </el-form-item>        
        <el-form-item>
          <el-button type="danger" @click="onSubmit('form')" style="width:120px;"></el-button>
          <el-button type="info" style="width:120px;"></el-button>
        </el-form-item>   
      </el-form>
    </section>
  </div>
</template>

<script>
  export default {
    data() {
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else {
          if (this.form.checkPass !== '') {
            this.$refs.form.validateField('checkPass');
          }
          callback();
        }
      };
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else if (value !== this.form.pass) {
          callback(new Error('!'));
        } else {
          callback();
        }
      };      
      return {
        title:'',
        form: {
          serial:'',
          pass:'',
          checkPass:'',
          name: '',
          phone: '',
          people: ''
        },
        rules: {
          serial: [
            { required: true, message: '', trigger: 'blur' },
            { min: 2, max: 10, message: ' 2  10 ', trigger: 'blur' }
          ],
          pass: [
            { validator: validatePass, trigger: 'blur' },
            { required: true, message: '', trigger: 'blur' },
            { min: 8, max: 12, message: ' 8  12 ', trigger: 'blur' },
            { pattern: /^[A-Za-z0-9\u4e00-\u9fa5]+$/, message: '' }
          ],
          checkPass: [
            { validator: validatePass2, trigger: 'blur' },
          ],
          name: [
            { required: true, message: '', trigger: 'blur' },
            // { min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }
          ],
          phone: [
            { required: true, message: '', trigger: 'blur' },
            // { min: 11, max: 5, message: ' 3  5 ', trigger: 'blur' }
          ],
          people: [
            { required: true, message: '', trigger: 'change' }
          ]
        },
      }
    },
    props:["serial"],//id
    mounted(){
      console.log(this.serial);
      if(this.serial){
        this.form.serial=this.serial;
      }else{
        this.title = ""
      }
    },
    methods:{
      handleEdit(index, rows){
        // console.log(rows[index]);
      },
      onSubmit(formName) {
        // console.log('submit!');
        this.$refs[formName].validate((valid) => {
          if (valid) {
            alert('submit!');
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      }
    }
  }
</script>

<style scoped>
/*  */
.main,header{
  width:100%;
  margin-top:-2px;
}

/*  */
.main>nav:nth-child(2){
  font-weight: bold;
  padding:10px 20px 20px 20px;
  background-color:#fff;
}

.main>nav:nth-child(2)>span{
  color:#5167B7;
}

  /*  */
 .el-input{
    display:inline-block;
    width:150px;
  }

  /*  */
  .main>h4{
    background-color:#FFF;
    padding: 10px 20px 20px 20px;
  }

  /*  */
  .el-form-item-clone{
    display: inline-block;
  }

  .el-form>.el-form-item:first-child>.el-form-item__content>.el-checkbox-group>.el-checkbox,.el-form>.el-form-item:last-child>.el-form-item__content>.el-checkbox-group>.el-checkbox{
    display: block;
  }

  .el-form,.main>section>h4{
    padding:10px 50px;
    /* display: flex;
    justify-content: space-around;
    background-color:#FFF; */
  }

  .main>section{
    background: #fff;
  }

  .main>section>h4{
    /* padding:10px; */
    border-bottom:1px solid #666666;
  }

  /* .el-form-item__label{
    margin:0 auto;
  } */

  .main>section>.el-button{
    margin:10px 0 10px 50px;
  }


</style>


