<template>
  <div class="main">
    <header>
      <!-- <h4> - </h4> -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path:'/manage/'}"></el-breadcrumb-item>                
        <el-breadcrumb-item :to="{ path:'/manage/'}"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>   
    </header>
    <nav>
      <span></span>
    </nav>
    <section style="background-color:#fff">
      <p>100</p>
      <section>
        <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange"></el-checkbox>
        <div style="margin: 10px 0;"></div>
        <el-checkbox-group v-model="checkedCities" @change="handleCheckedCitiesChange">
          <el-checkbox v-for="city in cities" :label="city" :key="city">{{city}}</el-checkbox>
        </el-checkbox-group>        
      </section>
    </section>
    <footer style="padding-top:30px;background:#FFFFFF">
      <div class="left-block">
        <el-button plain type="danger" @click="dialogDelete()">
          
        </el-button>
        <el-button plain type="success" @click="dialogFormVisible = true">
          
        </el-button>
      </div>
      <!--  -->
      <el-dialog title="" :visible.sync="dialogVisible" width="30%" :before-close="handleClose" style="text-align:center">
        <span>
          {{" " + checkedCities}}
        </span>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false"> </el-button>
          <el-button type="primary" @click='deleteSelect(cities)'> </el-button>
        </span>
      </el-dialog>
      <el-dialog title="" :visible.sync="dialogVisible" width="30%" :before-close="handleClose" style="text-align:center">
        <span>
          {{" " + checkedCities}}
        </span>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false"> </el-button>
          <el-button type="primary" @click='deleteSelect(cities)'> </el-button>
        </span>
      </el-dialog>
      <el-dialog title="" :visible.sync="dialogFormVisible" class="elDialog">
        *
        <el-select v-model="value" filterable placeholder="">
          <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false"> </el-button>
          <el-button type="primary" @click="dialogFormVisible = false"> </el-button>
        </div>
      </el-dialog>      
      <div class="right-block">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[100, 200, 300, 400]" :page-size="100" layout="total, sizes, prev, pager, next, jumper" :total="400">
        </el-pagination>
      </div>
    </footer>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        tableData: [{
          people: '',
          list: ["","",""]
        }, {
          people: '',
          list: ["","",""]
        }, {
          people: '',
          list: ["","",""]
        }],
        checkAll: false,
        checkedCities: ['', ''],
        cities: ['', '', '', '', '', '', ''],
        isIndeterminate: true,//indeterminate  checkbox 
        currentPage: 1,
        dialogVisible: false,
        dialogFormVisible: false,
        formLabelWidth: '120px',
        options: [{
          value: 'qq',
          label: ''
        }, {
          value: 'al',
          label: ''
        }, {
          value: 'hw',
          label: ''
        }, {
          value: 'bd',
          label: ''
        }, {
          value: 'pg',
          label: 'Phone'
        }],
        value: ''
      }
    },
    methods:{
      handleEdit(index, rows){
        // console.log(rows[index].people);
        this.$router.push("/manage/setamend/"+rows[index].people);
      },
      handleCheckAllChange(val) {
        this.checkedCities = val ? this.cities : [];
        this.isIndeterminate = false;
      },
      handleCheckedCitiesChange(value) {
        let checkedCount = value.length;
        this.checkAll = checkedCount === this.cities.length;
        this.isIndeterminate = checkedCount > 0 && checkedCount < this.cities.length;
        // console.log(this.isIndeterminate);//false
        // console.log(value);
      },
      handleSizeChange(val) {
        console.log(` ${val} `);
      },      
      handleCurrentChange(val) {
        console.log(`: ${val}`);
      },
      dialogDelete(){
        if(this.checkedCities.length >= 1){
          this.dialogVisible = true;
        }else{
          this.$message({
          message: '',
          type: 'warning'
        });
        }
      },
      deleteSelect(cities){
        if(cities.sort().toString() == this.checkedCities.sort().toString()){
          cities.splice(0, cities.length);
        }else{
          for(var j=0;j<cities.length;j++){
            for( var i=0;i<this.checkedCities.length;i++){
              if(cities[j] == this.checkedCities[i]){
                cities.splice(j, 1);
              }
            }            
          }
        };
        this.$message({
          type: 'success',
          message: ': '
        });
        this.dialogVisible = false;
      },
      handleClose(done) {//
        this.$confirm('')
        .then(_ => {
          done();
        })
        .catch(_ => {});
      }
    }
  }
</script>

<style scoped>
  /*  */
  .main,header{
    width:100%;
    margin-top:-2px;
  }

  /*  */
  .el-checkbox-group{
    display: inline-block;
    width: 600px;
  }

  .el-checkbox-group>.el-checkbox{
    width:33%;
    margin:10px 0;
  }

  /*  */
 footer{
    padding-bottom:20px;
    padding-right:50px;
    display:flex;   /**/
    justify-content:space-between;
    align-items:center;
  }

  .right-block,.left-block{
    display: inline-block;
  }

  .left-block{
    margin-left:35px;
  }

  .left-block>.el-button:nth-child(2){
    margin-left: 50px;
  }

  .main>section{
    padding-left: 50px;
  }

  .main>section>p{
    font-size:16px;
  }

  .main>section>section{
    margin:20px 0 0 20px;
  }

  .elDialog>.el-dialog>.el-dialog__body>.el-select{
    text-align: center;
    width: 230px;
    margin:10px;
  }
</style>


