<template>
  <div class="main">
    <header>
      <!-- <h4> - </h4> -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path:'/manage/companylist'}"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>  
    </header>
    <section>
      <!--  -->
      <nav>
        <el-select v-model="value1" placeholder="">
          <el-option v-for="item in options1" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
        <!--  -->
        <el-input placeholder="" prefix-icon="el-icon-search" v-model="search" clearable>
        </el-input>
        <!--  -->
        <el-button></el-button>
        <!--  -->
        <el-select v-model="value2" placeholder="" style="width:200px">
          <el-option v-for="item in options2" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
      </nav>
      <!--  -->
      <section>
        <!-- <el-table ref="multipleTable" tooltip-effect="dark" @selection-change="handleSelectionChange" :data="tableData.filter(data => !search || data.number.toLowerCase().includes(search.toLowerCase()) || data.name.toLowerCase().includes(search.toLowerCase()))" style="width: 100%"> -->
        <el-table ref="multipleTable" tooltip-effect="dark" @selection-change="handleSelectionChange" :data="tableData" style="width: 100%">
          <el-table-column type="selection" width="30">
          </el-table-column>
          <el-table-column prop="name" label="" width="100" sortable>
          </el-table-column>
          <el-table-column label="" prop="address" width="300" sortable>
          </el-table-column>
          <el-table-column label="" prop="person" width="130" sortable>
          </el-table-column>          
          <el-table-column label="" prop="phone" width="150" sortable>
          </el-table-column>
          <el-table-column label="" prop="project" width="150" sortable>
          </el-table-column>
          <el-table-column label="" prop="people" width="150" sortable>
          </el-table-column>
          <el-table-column label="" prop="earing" width="150" sortable>
            </el-table-column>
          <el-table-column fixed="right" label="" width="300">
            <template slot-scope="scope">
              <el-button @click.native.prevent="handleEdit(scope.$index, tableData)" type="text" size="small">
                
              </el-button>
              <router-link to="/manage/componentparticipation"></router-link>
              <router-link to="/manage/componentproject"></router-link>
            </template>
          </el-table-column>          
        </el-table>
      </section>
      <!--  -->
      <footer>
        <div class="left-block">
          <el-button type="danger" icon="el-icon-delete" @click="dialogDelete(tableData)"></el-button>
          <el-button type="success" icon="el-icon-plus" @click.native.prevent="handleEdit()"></el-button>
        </div>
        <!--  -->
        <el-dialog title="" :visible.sync="dialogVisible" width="30%" :before-close="handleClose" style="text-align:center">
          <span>
            {{" " + deleteName}}
          </span>
          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false"> </el-button>
            <el-button type="primary" @click='deleteSelect(tableData)'> </el-button>
          </span>
        </el-dialog>        
        <div class="right-block">
          <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage4" :page-sizes="[100, 200, 300, 400]" :page-size="100" layout="total, sizes, prev, pager, next, jumper" :total="400">
          </el-pagination>
        </div>
      </footer>
      <!--  -->
      <el-dialog :title="dio_title" :visible.sync="dialogFormVisible">
        <el-form :model="form">
          <el-form-item label="*" :label-width="formLabelWidth">
            <el-input v-model="form.name" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="*" :label-width="formLabelWidth">
            <el-input v-model="form.address" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="*" :label-width="formLabelWidth">
            <el-input v-model="form.person" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="*" :label-width="formLabelWidth">
            <el-input v-model="form.phone" autocomplete="off"></el-input>
          </el-form-item>                    
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false"> </el-button>
          <el-button type="primary" @click="dialogFormVisible = false"> </el-button>
        </div>
      </el-dialog>      
    </section>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        search: '',
        tableData: [{
          name: '',
          address: '',
          person: '',
          phone:13632756286,
          project:'23',
          people: '2',
          earing: '50000.00'
        }, {
          name: '',
          address: '',
          person: '',
          phone:'0755-71979843',
          project:'23',
          people: '8',
          earing: '150000.00'
        }, {
          name: '',
          address: '',
          person: '',
          phone:13632756286,
          project:'23',
          people: '5',
          earing: '21000.00'
        }],
        multipleSelection: [],//
        currentPage4: 4,
        UserList: [],
        options1: [{
          value: '1',
          label: ''
        }],
        value1: '',
        options2: [{
          value: '1',
          label: ''
        }, {
          value: '2',
          label: ''
        }],
        value2: '',
        dialogFormVisible: false,//
        dialogVisible: false,
        deleteName: [],
        dio_title:'',
        form: {
          name: '',
          address: '',
          person: '',
          phone: '',
          project: '',
          people: ''
        },
        formLabelWidth: '120px'//
      }
    },
    created(){
      // this.getUserList();
    },
    methods: {
      // ajax
      // getGoodsInfo(){
      //   var id=this.id;
      //   var url="goodslist/find?id="+id;
      //   this.$http.get(url).then(result=>{
      //     if(result.body.code==1){
      //       this.UserList=result.body.list;
      //     }
      //   });
      // },
      // handleClick() {
      //   this.$message.error('');        
      // },
      handleSelectionChange(val) {
        this.multipleSelection = val;
        
      },
      deleteRow(index, rows) {
        // console.log(rows[index]);
        rows.splice(index, 1);
      },
      dialogDelete(tableData){
        this.dialogVisible = true;
        var deleteItem=this.multipleSelection;
        this.deleteName.splice(0);//
        for(var j=0;j<tableData.length;j++){
          for( var i=0;i<deleteItem.length;i++){
            if(tableData[j] == deleteItem[i]){
              this.deleteName.push(deleteItem[i].name);
            }
          }
        }     
      },
      deleteSelect(tableData){
        let deleteItem=this.multipleSelection;
        // console.log(deleteItem);
        if(tableData.sort().toString() == deleteItem.sort().toString()){
          tableData.splice(0, tableData.length);
        }else{
          for(let j=0;j<tableData.length;j++){
          // console.log(tableData[j].number)
            for( let i=0;i<deleteItem.length;i++){
              if(tableData[j] == deleteItem[i]){
                // console.log(deleteItem[i].num+"+++");
                // console.log(tableData[j].num); 
                // console.log("===========");
                tableData.splice(j, 1);
                // console.log(tableData);
                // console.log(deleteItem[i].name);
                this.$message({
                  type: 'success',
                  message: ': '
                });
              }
            }
          }
        };
        this.dialogVisible = false;
      },
      handleEdit(index, rows){
        // this.$router.push("/manage/details");
        // console.log(rows[index]);
        this.dialogFormVisible = true;
        if(rows != undefined){
          this.dio_title = "";
          this.form.name = rows[index].name;
          this.form.address = rows[index].address;
          this.form.person = rows[index].person;
          this.form.phone = rows[index].phone;          
        }else{
          this.dio_title = "";          
          this.form.name = '';
          this.form.address = '';
          this.form.person = '';
          this.form.phone = '';       
        }
      },
      handleSizeChange(val) {
        console.log(` ${val} `);
      },
      handleCurrentChange(val) {
        console.log(`: ${val}`);
      },
      handleClose(done) {//
        this.$confirm('')
          .then(_ => {
            done();
          })
          .catch(_ => {});
      }      
    }
  }
</script>

<style scoped>
/*  */
.main,header{
  width:100%;
  margin-top:-2px;
}
/*  */
  nav{
    padding:15px;
  }
  /* .el-dropdown {
    vertical-align: top;
  }
  .el-dropdown + .el-dropdown {
    margin-left: 15px;
  } */
  nav div,nav input,nav button{
    margin-left:25px!important;
  }
  .el-icon-arrow-down {
    font-size: 12px;
  }
  .el-input{
    display:inline-block;
    width:300px;
  }
 footer{
    margin:10px;
    padding-right:50px;
    display:flex;   /**/
    justify-content:space-between;
    align-items:center;
 }
  .right-block,.left-block{
    display: inline-block;
  }
  .left-block{
    margin-left:35px;
  }

</style>



