<template>
  <div class="main">
    <header>
      <!-- <h4> - </h4> -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path:'/manage/setadministrator'}"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>       
    </header>
    <nav>
      
    </nav>
    <section>
      <el-table :data="tableData" style="width: 100%;">
        <el-table-column prop="serial" label="" width="150">
        </el-table-column>        
        <el-table-column prop="people" label="" width="120">
        </el-table-column> 
        <el-table-column prop="name" label="" width="120">
        </el-table-column> 
        <el-table-column prop="phone" label="" width="120">
        </el-table-column> 
        <el-table-column prop="state" label="" width="120">
        </el-table-column>                         
        <el-table-column label="" width="150">
            <template slot-scope="scope">
              <el-button @click.native.prevent="deleteRow(scope.$index, tableData)" type="text" style="color:red;" size="small">
                
              </el-button> 
              <el-button @click.native.prevent="handleEdit(scope.$index, tableData)" type="text" size="small">
                
              </el-button>           
            </template>
          </el-table-column>
      </el-table>
    </section>
    <footer style="padding-top:30px;background:#FFFFFF">
      <el-button plain type="success" style="margin-left:80px" @click.native.prevent="handleJump()">
        
      </el-button>
    </footer>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        tableData: [{
          serial: 'CS2017',
          people: '',
          name: '',
          phone: '13632756286',
          state: ''
        }, {
          serial: 'CS2013',
          people: '',
          name: '',
          phone: '13632756286',
          state: ''
        }, {
          serial: 'CS2019',
          people: '',
          name: '',
          phone: '13632756286',
          state: ''
        }],
        dialogVisible: false,
        deleteName: [{

        }]
      }
    },
    methods:{
      handleEdit(index, rows){
        // console.log(rows[index].people);
        this.$router.push("/manage/setadministratoradd/"+rows[index].serial);
      },
      handleJump(){
        this.$router.push("/manage/setadministratoradd");
        console.log(123);
      },
      deleteRow(index, rows) {
        console.log(rows[index].name);
        // rows.splice(index, 1);
        // this.dialogVisible = true;
        this.deleteName = rows[index].name;
        this.$confirm(` ${this.deleteName} , ?`, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          rows.splice(index, 1);
          this.$message({
            type: 'success',
            message: '!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });          
        });
      }
    }
  }
</script>

<style scoped>
/*  */
.main,header{
  width:100%;
  margin-top:-2px;
}

/*  */
.main>nav:nth-child(2){
  font-weight: bold;
  padding:10px 20px 20px 20px;
  background-color:#fff;
}

.main>nav:nth-child(2)>span{
  color:#5167B7;
}


</style>


