<template>
  <div class="main">
    <header>
      <!-- <h4> - </h4> -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path:'/manage/projectlist'}"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </header>
    <section>
      <!--  -->
      <nav>
        <!--  -->
        <!-- <el-dropdown split-button plain @click="handleClick">
          
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item></el-dropdown-item>
            <el-dropdown-item></el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown> -->
        <el-select v-model="value1" placeholder="">
          <el-option v-for="item in options1" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
        <!--  -->
        <!-- <el-input placeholder="" v-model="search" clearable autofocus>
          <i slot="prefix" class="el-input__icon el-icon-search"></i>
        </el-input> -->
        <el-input placeholder="" prefix-icon="el-icon-search" v-model="search" clearable>
        </el-input>
        <!--  -->
        <!-- <el-dropdown split-button plain @click="handleClick">
          
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item></el-dropdown-item>
            <el-dropdown-item></el-dropdown-item>
            <el-dropdown-item></el-dropdown-item>
            <el-dropdown-item></el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown> -->
        <!--  -->
        <el-button></el-button>        
        <!--  -->
        <!-- <el-dropdown split-button plain @click="handleClick">
          
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item></el-dropdown-item>
            <el-dropdown-item></el-dropdown-item>
            <el-dropdown-item></el-dropdown-item>
            <el-dropdown-item></el-dropdown-item>
            <el-dropdown-item></el-dropdown-item>
            <el-dropdown-item></el-dropdown-item>
            <el-dropdown-item></el-dropdown-item>
            <el-dropdown-item></el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown> -->
        <el-select v-model="value2" placeholder="" style="width:200px">
          <el-option v-for="item in options2" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
        <!--  -->
        <el-button type="success" icon="el-icon-upload" style="float:right;margin-right:150px;"></el-button>
      </nav>
      <!--  -->
      <section>
        <!-- <el-table ref="multipleTable" tooltip-effect="dark" @selection-change="handleSelectionChange" :data="tableData.filter(data => !search || data.number.toLowerCase().includes(search.toLowerCase()) || data.name.toLowerCase().includes(search.toLowerCase()))" style="width: 100%"> -->
        <el-table ref="multipleTable" tooltip-effect="dark" @selection-change="handleSelectionChange" :data="tableData" style="width: 100%">
          <el-table-column type="selection" width="30">
          </el-table-column>
          <el-table-column label="" prop="class" width="120" sortable>
          </el-table-column>   
          <el-table-column label="" prop="name" width="120" sortable>
          </el-table-column>                 
          <el-table-column prop="user" label="" width="120" sortable>
          </el-table-column>
          <el-table-column label="" prop="location" width="150" sortable>
          </el-table-column>
          <el-table-column label="()" prop="money" width="200" sortable>
          </el-table-column>
          <!-- <el-table-column prop="progress" label="" width="80" sortable>
          </el-table-column>
          <el-table-column prop="done" label="()" width="150" sortable>
          </el-table-column>
          <el-table-column prop="people" label="()" width="130" sortable>
          </el-table-column>
          <el-table-column label="()" prop="residue" width="130" sortable>
          </el-table-column>          
          <el-table-column label="" prop="state" width="100" sortable>
          </el-table-column> -->
          <el-table-column label="" prop="date" width="150" sortable>
          </el-table-column>    
          <el-table-column fixed="right" label="" width="300">
            <template slot-scope="scope">
              <el-button @click.native.prevent="details(scope.$index, tableData)" type="text" size="small">
                
              </el-button>
              <el-button @click.native.prevent="suggest(scope.$index, tableData)" type="text" size="small">
                
              </el-button>
              <el-button @click.native.prevent="personnel(scope.$index, tableData)" type="text" size="small">
                
              </el-button>
              <el-button @click.native.prevent="group(scope.$index, tableData)" type="text" size="small">
                
              </el-button>                         
            </template>
          </el-table-column>
        </el-table>
      </section>
      <!--  -->
      <footer>
        <div class="left-block">
          <el-button type="danger" icon="el-icon-delete" @click="dialogDelete(tableData)"></el-button>
          <el-button type="info" icon="el-icon-warning"></el-button>
        </div>
        <!--  -->
        <el-dialog title="" :visible.sync="dialogVisible" width="30%" :before-close="handleClose" style="text-align:center">
          <span>
            {{" " + deleteName}}
          </span>
          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false"> </el-button>
            <el-button type="primary" @click='deleteSelect(tableData)'> </el-button>
          </span>
        </el-dialog>        
        <div class="right-block">
          <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage4" :page-sizes="[100, 200, 300, 400]" :page-size="100" layout="total, sizes, prev, pager, next, jumper" :total="400">
          </el-pagination>
        </div>
      </footer>
    </section>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        search: '',
        tableData: [{
          class:'',
          name: '',
          user:'',
          location:'--',
          money:'1100000045',
          progress:'100%',
          done:'10000000',
          people:'99',
          residue:'3',
          date:'2016-05-22 10:00',
          state:''
        }, {
          class:'',
          name: '',
          user:'',
          location:'--',
          money:'7770000045',
          progress:'50%',
          done:'456000000',
          people:'89',
          residue:'2',
          date:'2015-05-12 18:10',
          state:''
        }, {
          class:'',
          name: '',
          user:'',
          location:'--',
          money:'6660000045',
          progress:'80%',
          done:'99000000',
          people:'59',
          residue:'1',
          date:'2017-05-02 12:30',
          state:''
        }, {
          class:'',
          name: '',
          user:'',
          location:'--',
          money:'5550000045',
          progress:'70%',
          done:'188000000',
          people:'100',
          residue:'2',
          date:'2018-05-05 11:00',
          state:''
        }],
        multipleSelection: [],
        currentPage1: 5,
        currentPage2: 5,
        currentPage3: 5,
        currentPage4: 4,
        options1: [{
          value: '1',
          label: ''
        }, {
          value: '2',
          label: ''
        }, {
          value: '3',
          label: ''
        }],
        value1: '',
        options2: [{
          value: '1',
          label: ''
        }, {
          value: '2',
          label: ''
        },{
          value: '3',
          label: ''
        }, {
          value: '4',
          label: ''
        },{
          value: '5',
          label: ''
        }, {
          value: '6',
          label: ''
        },{
          value: '7',
          label: ''
        }, {
          value: '8',
          label: ''
        }],
        value2: '',
        dialogVisible: false,
        deleteName: [] 
      }
    },
    methods: {
      handleClick() {
        this.$message.error('');
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      deleteRow(index, rows) {

      },
      dialogDelete(tableData){
        this.dialogVisible = true;
        var deleteItem=this.multipleSelection;
        this.deleteName.splice(0);//
        for(var j=0;j<tableData.length;j++){
          for( var i=0;i<deleteItem.length;i++){
            if(tableData[j] == deleteItem[i]){
              this.deleteName.push(deleteItem[i].name);
            }
          }
        }     
      },
      deleteSelect(tableData){
        let deleteItem=this.multipleSelection;
        // console.log(deleteItem);
        if(tableData.sort().toString() == deleteItem.sort().toString()){
          tableData.splice(0, tableData.length);
        }else{
          for(let j=0;j<tableData.length;j++){
          // console.log(tableData[j].number)
            for( let i=0;i<deleteItem.length;i++){
              if(tableData[j] == deleteItem[i]){
                // console.log(deleteItem[i].num+"+++");
                // console.log(tableData[j].num); 
                // console.log("===========");
                tableData.splice(j, 1);
                // console.log(tableData);
                // console.log(deleteItem[i].name);
                this.$message({
                  type: 'success',
                  message: ': '
                });
              }
            }
          }
        };
        this.dialogVisible = false;
      },
      handleEdit(index, rows){
        
      },
      details(){
        this.$router.push("/manage/projectdetails");
      },
      suggest(){
        this.$router.push("/manage/projectequity");
      },
      personnel(){
        this.$router.push("/manage/projectnotice");
      },
      group(){
        this.$router.push("/manage/projectearing");
      },
      handleSizeChange(val) {
        console.log(` ${val} `);
      },
      handleCurrentChange(val) {
        console.log(`: ${val}`);
      },
      handleClose(done) {//
        this.$confirm('')
          .then(_ => {
            done();
          })
          .catch(_ => {});
      }      
    }
  }
</script>

<style scoped>
/*  */
.main,header{
  width:100%;
  margin-top:-2px;
}

/*  */
  nav{
    padding:15px;
  }
  .el-dropdown {
    vertical-align: top;
  }
  .el-dropdown + .el-dropdown {
    margin-left: 15px;
  }
  nav div,nav input,nav button{
    margin-left:25px!important;
  }
  nav div:last-child{
    margin-left:100px!important;
  }
  .el-icon-arrow-down {
    font-size: 12px;
  }
  .el-input{
    display:inline-block;
    width:300px;
  }
/*  */
 footer{
    margin:10px;
    padding-right:50px;
    display:flex;   /**/
    justify-content:space-between;
    align-items:center;
 }
  .right-block,.left-block{
    display: inline-block;
  }
  .left-block{
    margin-left:35px;
  }
 
</style>



